"use client";
import { useState } from "react";
import { Pencil, Archive } from "lucide-react";
import { Button } from "../ui/button";
import clsx from "clsx";
import { updateClassroom, archiveClassroom } from "@/lib/api";

type Classroom = {
  id: number;
  name: string;
  totalStudents: number;
  color: string;
};
import type * as React from "react";

type Props = {
  classrooms: Classroom[];
  setClassrooms: React.Dispatch<React.SetStateAction<Classroom[]>>;
};

const ITEMS_PER_PAGE = 8;


export default function ClassroomTable({ classrooms, setClassrooms }: Props) {
  const [currentPage, setCurrentPage] = useState(1);
  const totalPages = Math.ceil(classrooms.length / ITEMS_PER_PAGE);
  const startIdx = (currentPage - 1) * ITEMS_PER_PAGE;
  const currentItems = classrooms.slice(startIdx, startIdx + ITEMS_PER_PAGE);

  const [editModalOpen, setEditModalOpen] = useState(false);
  const [archiveModalOpen, setArchiveModalOpen] = useState(false);
  const [selectedClassroom, setSelectedClassroom] = useState<Classroom | null>(null);
  const [newClassName, setNewClassName] = useState("");

  const handleEdit = (classroom: Classroom) => {
    setSelectedClassroom(classroom);
    setNewClassName(classroom.name);
    setEditModalOpen(true);
  };

  const handleArchive = (classroom: Classroom) => {
    setSelectedClassroom(classroom);
    setArchiveModalOpen(true);
  };

const confirmEdit = async () => {
  if (!selectedClassroom) return;
  try {
    await updateClassroom(selectedClassroom.id, newClassName);
    setClassrooms((prev) =>
      prev.map((cls) =>
        cls.id === selectedClassroom.id ? { ...cls, name: newClassName } : cls
      )
    );
  } catch (err) {
    console.error(err);
  }
  setEditModalOpen(false);
};

const confirmArchive = async () => {
  if (!selectedClassroom) return;
  try {
    await archiveClassroom(selectedClassroom.id);
    setClassrooms((prev) =>
      prev.filter((cls) => cls.id !== selectedClassroom.id)
    );
  } catch (err) {
    console.error(err);
  }
  setArchiveModalOpen(false);
};


  return (
    <div className="space-y-4 relative">
      {/* Table */}
      <div className="overflow-x-auto rounded-xl border border-gray-200 shadow-sm">
        <table className="min-w-full text-sm text-left">
          <thead className="bg-gray-50 text-gray-600 uppercase text-xs hidden sm:table-header-group">
            <tr>
              <th className="px-6 py-4">Classroom Name</th>
              <th className="px-6 py-4">Total Students</th>
              <th className="px-6 py-4">View</th>
              <th className="px-6 py-4">Action</th>
            </tr>
          </thead>
          <tbody className="divide-y divide-gray-100 bg-white">
            {currentItems.map((classroom) => (
              <tr key={classroom.id}>
                <td className="px-6 py-4 flex items-center gap-3">
                  <div className={clsx("w-8 h-8 rounded-full text-white flex items-center justify-center font-bold", classroom.color)}>
                    {classroom.name.charAt(0)}
                  </div>
                  <div className="text-sm font-medium">{classroom.name}</div>
                </td>
                <td className="px-6 py-4">{classroom.totalStudents}</td>
                <td className="px-6 py-4 text-blue-600 hover:underline cursor-pointer">View Classroom</td>
                <td className="px-6 py-4 flex gap-2">
                  <button onClick={() => handleEdit(classroom)} className="text-blue-500 hover:text-blue-700">
                    <Pencil size={18} />
                  </button>
                  <button onClick={() => handleArchive(classroom)} className="text-orange-500 hover:text-orange-600">
                    <Archive size={18} />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      <div className="flex items-center gap-4 justify-center mt-4">
        <Button className="bg-[#25388C] hover:bg-[#1e2e6d]" onClick={() => setCurrentPage((p: number) => Math.max(p - 1, 1))} disabled={currentPage === 1}>
          Previous
        </Button>
        <span className="text-sm text-gray-600">
          Page {currentPage} of {totalPages}
        </span>
        <Button className="bg-[#25388C] hover:bg-[#1e2e6d]" onClick={() => setCurrentPage((p: number) => Math.min(p + 1, totalPages))} disabled={currentPage === totalPages}>
          Next
        </Button>
      </div>
      {/* Edit Modal */}
      {editModalOpen && (
        <div className="fixed inset-0 z-50 bg-black/50 flex items-center justify-center">
          <div className="bg-white p-6 rounded-lg shadow-md w-[90%] max-w-md">
            <div className="flex justify-between items-center mb-4">
              <h2 className="font-semibold text-lg">Edit Classroom Name</h2>
            </div>
            <input
              className="w-full px-4 py-2 border rounded-md mb-4"
              value={newClassName}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => setNewClassName(e.target.value)}
            />
            <Button onClick={confirmEdit} className="w-full bg-[#25388C] hover:bg-[#1e2e6d]">Save</Button>
          </div>
        </div>
      )}

      {/* Archive Modal */}
      {archiveModalOpen && (
        <div className="fixed inset-0 z-50 bg-black/50 flex items-center justify-center">
          <div className="bg-white p-6 rounded-lg shadow-md w-[90%] max-w-sm text-center">
            <p className="font-semibold text-gray-800 mb-6">Are you sure you want to archive this?</p>
            <div className="flex justify-center gap-4">
              <Button variant="secondary" onClick={() => setArchiveModalOpen(false)}>
                Cancel
              </Button>
              <Button variant="destructive" onClick={confirmArchive}>
                Confirm
              </Button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}